html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  background-color: #f8fafc;
}

p {
  font-size: 2rem;
  font-weight: 500;
  margin: 0;
  text-align: justify;
  font-family: "Inter", sans-serif;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

h1,
h2,
h4 {
  margin: 0 0 5rem 0;
  cursor: default;
  font-weight: 700;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 4.5rem;
}
@media (max-width: 600px) {
  h2 {
    font-size: 4rem !important;
  }
}

h3 {
  font-size: 2.5rem;
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h2 {
  font-size: 5.5rem;
  font-weight: 800;
}

main .container {
  margin: 3% 5%;
  background-color: #ffffff;
  border: 1.5px solid #cac9c9;
  width: auto;
  height: 100%;
}
main .container .header {
  background-color: #ed3456;
  color: #ffffff;
  border-bottom: 1.5px solid #cac9c9;
  padding: 1.5rem;
}
main .container .header p {
  text-align: center;
}
main .container .info {
  text-align: center;
  padding: 2rem;
}

table button,
.edit {
  background-color: #6fd46f !important;
}

table button:hover,
.edit:hover {
  background-color: #48c848 !important;
}

.status_0 {
  color: #6fd46f !important;
  font-weight: 700;
}

.status_1 {
  color: #ed3456 !important;
  font-weight: 700;
}

form input,
form select,
input[type=search] {
  padding: 8px;
  border-radius: 5px;
  border: 1.5px solid #cac9c9;
  border-style: solid;
  width: 50%;
  margin-left: 3rem;
}

input[type=search] {
  width: auto;
}

header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #ed3456;
  color: #ffffff;
  align-items: center;
  padding: 1.5% 5%;
}
header a {
  text-decoration: none;
  color: #ffffff;
}
header a:hover {
  color: #cac9c9;
}
header .pages {
  width: 100%;
  display: flex;
  gap: 3%;
  justify-content: center;
}

.info .input {
  margin-right: 25%;
  margin-bottom: 2rem;
  display: flex;
  justify-content: right;
  align-items: center;
}
.info button {
  background-color: #ed3456;
  padding: 1.2rem 2rem;
  border: none;
  border-radius: 5px;
  color: #ffffff;
}
.info button:hover {
  background-color: #db1338;
}

#tabla {
  padding-top: 1rem;
}
#tabla th,
#tabla td {
  border: 0.8px solid #cac9c9 !important;
}
#tabla .precio {
  width: 12% !important;
}
#tabla .mid {
  width: 10% !important;
}
#tabla .min {
  width: 7% !important;
}
#tabla .edit {
  padding: 5% 25% !important;
}

.botones {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

#tabla input,
#tabla select {
  margin-left: 0;
  width: auto;
}
#tabla .delete {
  background-color: #ed3456 !important;
}

#new_ing {
  background-color: #6fd46f;
}

#new_ing:hover {
  background-color: #48c848;
}

.radio {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}
.radio input {
  width: auto !important;
  margin-left: 1%;
  margin-right: 5%;
}

#pro,
#cantidad_pro,
.existente input,
.existente select {
  border: 1.5px solid #cac9c9;
  border-radius: 5px;
  margin-left: 3rem;
  padding: 8px;
  width: 50%;
}

.nuevo,
.existente {
  display: none;
}